





























import Vue from 'vue'

export default Vue.extend
({
    components: 
    {
        
    },
    data()
    {
        // 0 //
        return{
            using_demo: '',
        }
    },
    methods:
    {
        routeToHome() :void
        {
            this.$router.push({path: '/'})
        },
        routeToDemo() :void
        {
            console.log(this.using_demo)

            if(this.using_demo != this.$router.currentRoute.fullPath)
            {
                this.$router.push({path: `/demos/${this.using_demo}`})
            }
            else
            {
                this.$router.go(0)
            }
        },
        refresh() :void
        {
            this.$router.go(0)
        },
    },
})
