

























































































import dragScroll from '../../drag-scroll'

import Vue from 'vue'

export default Vue.extend
({
    components: 
    {
        
    },
    data()
    {
        return{

        }
    },
    methods:
    {
        setDragScroll_1to1() :void
        {
            dragScroll
            (
                {
                    element: this.$refs.element,
                    container: this.$refs.container,
                }
            )
        },
        setDragScroll_50to50() :void
        {
            dragScroll
            (
                {
                    element: this.$refs.element,
                    container: this.$refs.container,
                    x:
                    {
                        drag_scale: 50,
                        scroll_scale: 50,
                    },
                    y:
                    {
                        drag_scale: 50,
                        scroll_scale: 50,
                    },
                }
            )
        },
        disableDragScroll() :void
        {
            dragScroll
            (
                {
                    element: this.$refs.element,
                    container: this.$refs.container,
                    disabled: true,
                }
            )
        },
    },
    mounted()
    {
        
    },
})
